import _get from 'lodash.get';
import { TJurisdictionalCost } from '../../../../../types';

export const getItemsKeys = (jurisdictionalCost: TJurisdictionalCost[]) =>
	jurisdictionalCost.reduce<string[]>(
		(values, jurisdictiona) => [...values, _get(jurisdictiona, 'ItemKey')],
		[]
	);

export const prepareInitValue = (jurisdictionalCost: TJurisdictionalCost[]) =>
	jurisdictionalCost.reduce<{ [key: string]: string }>(
		(values, jurisdictiona) => {
			const itemKey = _get(jurisdictiona, 'ItemKey');
			const value = _get(jurisdictiona, 'PayorIdOverride') || '';
			return { ...values, [`${itemKey}`]: value };
		},
		{}
	);
